/**
 * admin/css/style.css
 * General LBD Plugin admin styles.
 */

/*** LBD Plugin Dashboard ***/

/** Variables **/
:root {
    --lbd-admin-bg-colour-header: #fefefe;
    --lbd-admin-bg-tab-active: #f0f0f1;
    --lbd-admin-bg-hover-tab-inactive: #e6e6e6;
    --lbd-admin-pad-screen-heading: 0.5rem 0;
    --lbd-admin-pad-tab: 0.25rem 0.75rem;
    --lbd-admin-margin-screen-heading: 0 0 1.5rem;
    --lbd-admin-border-colour-tab: #e6e6e6;
    --lbd-admin-border-tab: 0.125rem solid var(--lbd-admin-border-colour-tab);
    --lbd-admin-border-radius-tab: 0.5rem 0.5rem 0 0;
    --lbd-admin-font-screen-heading-size: 1.5rem;
    --lbd-admin-font-screen-heading-weight: 400;
    --lbd-admin-font-tab-colour: #3e3e3e;
    --lbd-admin-font-tab-size: 1rem;
    --lbd-admin-font-tab-weight: 500;
}

/** Customs **/
#wpcontent {
    padding-left: 0;
}

#wpcontent #wpbody #wpbody-content .wrap {
    margin: 0;
}

.notice, .error, .updated {
    margin: 0 !important;
}

/** General Classes **/
#wpcontent #wpbody #wpbody-content .pagewrap * {
    box-shadow: none !important;
}

/** General Classes **/
a {
    text-decoration: none;
}

/* Padding */
.pad-bottom-quart {
    padding-bottom: 0.25rem;
}

.pad-top-1 {
    padding-top: 1rem;
}

.pad-right-2 {
    padding-right: 2rem;
}

.pad-bottom-2 {
    padding-bottom: 2rem;
}

.pad-left-2 {
    padding-left: 2rem !important;
}

/** Admin Header **/
.lbd-admin-header {
    background: var(--lbd-admin-bg-colour-header);
}

/** Admin Header > Screen Heading **/
.lbd-admin-header .screen-heading {
    padding: var(--lbd-admin-pad-screen-heading);
    margin: var(--lbd-admin-margin-screen-heading);
    font-size: var(--lbd-admin-font-screen-heading-size);
    font-weight: var(--lbd-admin-font-screen-heading-weight);
}

/** Admin Header > Settings Tabs **/
.lbd-admin-header .nav .tab {
    padding: var(--lbd-admin-pad-tab);
    border: var(--lbd-admin-border-tab);
    border-bottom: none;
    border-radius: var(--lbd-admin-border-radius-tab);
    color: var(--lbd-admin-font-tab-colour);
    font-size: var(--lbd-admin-font-tab-size);
    font-weight: var(--lbd-admin-font-tab-weight);
}

.lbd-admin-header .nav .tab:not(.active):hover,
.lbd-admin-header .nav .tab:not(.active):focus,
.lbd-admin-header .nav .tab:not(.active):active {
    background: var(--lbd-admin-bg-hover-tab-inactive);
}

.lbd-admin-header .nav .tab.active,
.lbd-admin-header .nav .tab.active:hover,
.lbd-admin-header .nav .tab.active:focus,
.lbd-admin-header .nav .tab.active:active {
    cursor: default;
    pointer-events: none !important;
}

.lbd-admin-header .nav .active {
    background: var(--lbd-admin-bg-tab-active);
}

/** Tab Content **/
.tab-content .tab-pane {
    display: none;
}

.tab-content .tab-pane.active {
    display: block;
}